@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,800&display=swap);
body{
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}

@font-face{
  font-family:'Blorado';
  src:url(/shoprrr/static/media/SofiaProLight-webfont.63bfaeaf.woff2) format("woff2");
  font-weight:400;
  font-display:fallback;
  font-style:normal
}

a {
  text-decoration: none;
  color: black;
  font-size: 14px;
  letter-spacing: 0.5px;
}
a, p, h3, h4, h5, h6 {
  letter-spacing: 0.5px;
}
.h1, h1 {
  font-family: blorado !important;
}
*{
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.pageSlider-enter.fades{
  /* transform: translateY(100%); */
  opacity: 0;
}
.pageSlider-exit.fades{
  opacity: 1;
  /* transform: translateY(-100%); */
}
.pageSlider-enter.pageSlider-enter-active{
  opacity:1;
  /* transform: translateY(0%); */
  transition:all 1s ease;
}
.menu-item {
  min-width: 30%;
  height: 350px;
  flex: 1 1 auto;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  background-position: center;
  background-size: cover;
  margin: 0 7.5px 15px;
  overflow: hidden;
}
.menu-item.large {
  height: 380px;
}
.menu-item:first-child {
  margin-right: 7.5px;
}
.menu-item:last-child {
  margin-left: 7.5px;
}
.menu-item:hover {
  cursor: pointer;
}
.menu-item:hover .background-image {
  transform: scale(1.1);
  transition: transform 6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.menu-item .background-image {
  height: 100%;
  width: 100%;
  background-blend-mode: overlay;
  background-color: rgba(255, 255, 255, 0.2117647059);
  background-size: cover;
  background-position: center;
}
.menu-item .content {
  height: 90px;
  padding: 0 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
}
.menu-item .content .title {
  font-weight: 800;
  margin-bottom: 6px;
  font-size: 23px;
  text-transform: capitalize;
  color: #000;
}
.menu-item .content .subtitle {
  font-weight: lighter;
  font-size: 13px;
  color: #000;
}
.directory-menu {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.collection-item {
  display: flex;
  flex-direction: column;
  height: 280px;
  align-items: center;
  position: relative;
  border-radius: 12px;
  /* background: white; */
  box-shadow: 4px 4px 15px rgba(47, 47, 47, 0.0588235294);
  border: 1px solid #f3f3f3;
}
.collection-item .image {
  width: 100%;
  height: 320px;
  background-size: cover;
  background-position: center;
}
.collection-item .custom-button {
  width: 80%;
  opacity: 0.7;
  position: absolute;
  top: 10rem;
  display: none;
  height: 45px !important;
  font-size: 13px !important;
  font-weight: 900;
  line-height: 45px;
  border: none;
}
.collection-item:hover .image {
  opacity: 0.8;
}
.collection-item:hover .custom-button {
  opacity: 0.85;
  display: flex;
}
.collection-item .collection-footer {
  width: 100%;
  padding: 10.5px 23px;
  font-size: 15px;
}
.collection-item .collection-footer .name {
  margin-bottom: 1px;
  display: block;
}
.collection-item .collection-footer .price {
  display: block;
  margin-bottom: 1px;
  font-weight: 700;
}
.collection-page {
  display: flex;
  flex-direction: column;
}
.collection-page .title {
  font-size: 38px;
  margin: 0 auto 30px;
}
.collection-page .items {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  grid-template-rows: 1fr;
  grid-column-gap: 12px;
}
.collection-page .items .collection-item {
  margin-bottom: 30px;
}
.collection-preview {
  margin-bottom: 30px;
}
.collection-preview .title {
  font-size: 28px;
  margin-bottom: 25px;
}
.collection-preview .preview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  grid-template-rows: 1fr;
  grid-column-gap: 12px;
}
.collections-overview {
  display: flex;
  flex-direction: column;
}
.cart-icon {
  width: 45px;
  height: 11px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.cart-icon .shopping-icon {
  width: 24px;
  height: 24px;
}
.cart-icon .item-count {
  position: absolute;
  font-size: 10px;
  font-weight: bold;
  bottom: -5px;
}
.cart-dropdown {
  animation-iteration-count: 1;
  animation-duration: 0.5s;
  animation-name: slide;
  position: fixed;
  width: 320px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-shadow: 0 6px 11px rgba(0, 0, 0, 0.1098039216);
  border: 1px solid white;
  background-color: white;
  bottom: 0;
  right: 0px;
  z-index: 5;
}
.cart-dropdown .empty-message {
  font-size: 18px;
  margin: 50px auto;
}
.cart-dropdown .cart-items {
  height: 95vh;
  display: flex;
  flex-direction: column;
  overflow: scroll;
}
.cart-dropdown button {
  margin-top: auto;
}
.cart-dropdown h4 {
  font-size: 17px;
  font-family: blorado;
  font-weight: bolder;
  padding: 14px 12px;
  margin-bottom: 2rem;
  border-bottom: 1px solid #dedede;
}
.cart-dropdown .closePath {
  cursor: pointer;
  font-size: 14px;
}

@keyframes slide {
  from {
    transform: translateX(140px);
  }
  to {
    transform: translateX(0px);
  }
}
.cart-item {
  width: 100%;
  display: flex;
  height: 80px;
  margin-bottom: 15px;
}
.cart-item img {
  width: 30%;
  object-fit: cover;
}
.cart-item .item-details {
  width: 65%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 10px 20px;
}
.cart-item .item-details .name {
  font-size: 16px;
  font-family: blorado;
  font-weight: 600;
}
.cart-item .item-details .price {
  font-size: 13px;
  margin-top: 11px;
}
.cart-item .delete {
  width: 10%;
  display: flex;
  align-content: center;
  align-self: center;
  font-weight: 900;
  background: #909090;
  justify-content: center;
  font-family: blorado;
  cursor: pointer;
  color: #fff;
  border-radius: 50%;
  padding: 5px 3px;
  font-size: 11px;
}
.sign-in {
  display: flex;
  flex-direction: column;
}
.sign-in span {
  margin-bottom: 1rem;
  font-size: 14px;
  text-align: center;
}
.sign-in h2 {
  font-family: blorado;
  font-weight: 800;
  text-align: center;
}
.sign-in .title {
  margin: 10px 0;
}
.group {
  position: relative;
  margin: 45px 0;
}
.group .form-input {
  background: none;
  background-color: white;
  color: grey;
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 100%;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid grey;
  margin: 25px 0;
}
.group .form-input:focus {
  outline: none;
}
.group .form-input:focus ~ .form-input-label {
  top: -14px;
  font-size: 12px;
  color: black;
}
.group input[type=password] {
  letter-spacing: 0.3em;
}
.group .form-input-label {
  color: grey;
  font-size: 16px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 300ms ease all;
}
.group .form-input-label.shrink {
  top: -14px;
  font-size: 12px;
  color: black;
}
.sign-up {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.sign-up span {
  margin-bottom: 1rem;
  font-size: 14px;
  text-align: center;
}
.sign-up h2 {
  font-family: blorado;
  font-weight: 800;
  text-align: center;
}
.sign-up .title {
  margin: 10px 0;
}
.sign-up button {
  margin: auto;
}
.sign-in-and-sign-up {
  display: flex;
  align-items: center;
  padding: 0px 1rem;
  min-height: 60vh;
  justify-content: center;
  margin: 30px auto;
}
.sign-in-and-sign-up input {
  font-size: 14px !important;
}

.logo {
  height: 70px;
  margin: 0rem auto 1rem;
  display: block;
  text-align: center;
}

.routes a {
  font-weight: 500;
  font-size: 16px;
  color: #adadad;
}
.routes a.activetab {
  border-bottom: 2px solid #000;
  color: #000;
}

.routes {
  display: flex;
  justify-content: space-around;
  width: 47%;
  margin: 2rem auto;
}
.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;
}
@media (max-width: 720px) {
  .checkout-page {
    width: 90%;
  }
}
.checkout-page .checkout-header {
  width: 100%;
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e2e2e2;
}
@media (max-width: 720px) {
  .checkout-page .checkout-header {
    width: 99%;
    margin-bottom: 1rem !important;
  }
  .checkout-page .checkout-header span {
    display: none;
  }
  .checkout-page .checkout-header .header-block:first-child span {
    display: block !important;
  }
}
.checkout-page .checkout-header span {
  font-size: 15px;
}
.checkout-page .checkout-header .header-block {
  text-transform: capitalize;
  width: 23%;
}
.checkout-page .checkout-header .header-block:last-child {
  width: 8%;
}
.checkout-page .total {
  margin-top: 30px;
  margin-left: auto;
  font-size: 36px;
}
.checkout-page button, .checkout-page a {
  margin-left: auto;
  margin-top: 15px;
  border: none !important;
  box-shadow: none !important;
}
.checkout-page button span, .checkout-page a span {
  padding: 8px 37px !important;
  height: auto !important;
  background: #00afaf !important;
  border: none !important;
  box-shadow: none !important;
}
.checkout-page .test-warning {
  text-align: center;
  margin-top: 40px;
  font-size: 18px;
  color: red;
}
.checkout-item {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid #e2e2e2;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;
}
@media (max-width: 720px) {
  .checkout-item {
    display: block;
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1098039216);
    background: white;
    padding: 20px;
    margin-bottom: 1rem;
    border-radius: 11px;
  }
  .checkout-item .name {
    font-weight: 700;
  }
  .checkout-item .remove-button {
    float: right;
    margin-top: -2rem;
    background: #efefef;
    padding: 3px 8px !important;
    /* display: flex; */
    font-size: 16px;
    /* justify-content: center; */
    border-radius: 50%;
  }
}
.checkout-item .image-container {
  width: 23%;
  padding-right: 15px;
  height: 200px;
}
@media (max-width: 720px) {
  .checkout-item .image-container {
    width: 100%;
    margin-bottom: 1rem;
    padding: 0;
  }
  .checkout-item .image-container img {
    border-radius: 8px;
  }
}
.checkout-item .image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.checkout-item .name,
.checkout-item .quantity,
.checkout-item .price {
  width: 23%;
  font-size: 16px;
  padding: 0 7px;
}
.checkout-item .quantity {
  display: flex;
}
.checkout-item .quantity .arrow {
  cursor: pointer;
}
.checkout-item .quantity .value {
  margin: 0 10px;
}
.checkout-item .remove-button {
  padding-left: 12px;
  cursor: pointer;
}
